import React, { useEffect, useState } from 'react'
import { useLocation } from 'react-router-dom'
import { useInView } from 'react-intersection-observer'
import Filters from '../../../components/Sub/Filters/Filters'
import FeedCard from '../../../components/Sub/FeedCard/FeedCard'
import useStyles from './ConsultantFeedPage.style'
import api from '../../../api/api'
import useAsync from '../../../hooks/useAsync'
import useFilters from '../../../hooks/useFilters'
import { API_REPORTS_ENDPOINT, API_GET_FILTERS_STUDENTS } from '../../../utils/constants'
import ExportFeed from '../../../components/Sub/ExportDialogs/ExportFeed/ExportFeed'

const ConsultantFeedPage = () => {
  const [exportDialogOpen, setExportDialogOpen] = useState(false)
  const [shouldExportFeedData, setShouldExportFeedData] = useState(false)
  const [filtersJSON, setFiltersJSON] = useState({})
  const location = useLocation()
  const studentId = location.state?.studentId
  const studentName = location.state?.studentName
  const hook = useFilters({ id: studentId, name: studentName })
  const [reports, setReports] = useState(null)
  const { run } = useAsync({
    data: [],
    error: null,
  })

  const classes = useStyles()

  const { ref, inView } = useInView({
    threshold: 0.5,
  })

  const getReports = async (pageParams = { limit: 3 }) => {
    let requestParams
    try {
      if (hook.filters.id) requestParams = { profile_id: hook.filters.id }
      else requestParams = { ...hook.filters, profile_id: hook.filters.studentId, studentId: undefined }
      if (requestParams?.end_date) requestParams = { ...requestParams, end_date: `${requestParams?.end_date}T23:59` }
      const response = await api.get(API_REPORTS_ENDPOINT, {
        params: { ...requestParams, ...pageParams },
      })
      if (!pageParams?.page) {
        setReports(response.data)
      } else {
        setReports({ ...response.data, results: [...reports.results, ...response.data.results] })
      }
      return Promise.resolve(response.data)
    } catch (e) {
      return Promise.reject(e.response)
    }
  }

  const getFilters = async () => {
    try {
      const response = await api.get(API_GET_FILTERS_STUDENTS)
      setFiltersJSON(response.data)
      return Promise.resolve(response.data)
    } catch (e) {
      return Promise.reject(e.response)
    }
  }

  useEffect(() => run(getFilters()), [])

  useEffect(() => {
    if (hook.filters.id) run(getReports(hook.filters.id))
    else if (hook.filters.start_date) run(getReports())
  }, [hook.filters])

  useEffect(() => {
    if (reports && reports.next) {
      const nextPageUrl = new URL(reports.next)
      const nextPageNumber = nextPageUrl.searchParams.get('page')
      if (inView) run(getReports({ limit: 3, page: nextPageNumber }))
    }
  }, [inView, reports])

  const handleFeedExportOpen = () => {
    setShouldExportFeedData(true)
    setExportDialogOpen(true)
  }

  const handleFunnelExportOpen = () => {
    setShouldExportFeedData(false)
    setExportDialogOpen(true)
  }

  return (
    <main className={classes.root}>
      <ExportFeed
        openState={[exportDialogOpen, setExportDialogOpen]}
        directory={filtersJSON}
        shouldExportFeedData={shouldExportFeedData}
      />
      <Filters
        filterType="feed"
        hook={hook}
        filtersJSON={filtersJSON}
        handleFeedExport={handleFeedExportOpen}
        handleFunnelExport={handleFunnelExportOpen}
        handleDownload={() => setExportDialogOpen(true)}
      />
      <div>
        {reports?.results?.map((report, index) => {
          if (reports.results.length === index + 1) {
            return (
              <div>
                <FeedCard ref={ref} id={report.id} key={report.id} profile={report.profile} report={report} />
              </div>
            )
          }
          return (
            <div>
              <FeedCard id={report.id} key={report.id} profile={report.profile} report={report} />
            </div>
          )
        })}
      </div>
    </main>
  )
}
export default ConsultantFeedPage
